
var utils = require('../utils')

module.exports = {
  watch: false
, priority: -2 // ref  component 
, unLink: function() {
    if(!utils.isArray(this.ref)) {
      this.vm.$refs[this.path] = null;
    }
  }
, link: function() {
    var vm = this.vm
    // `repeat`  `ref`  `viewmodel`
    if(vm.__repeat){
      if(!vm.$index) {
        vm.$parent.$refs[this.path] = vm.__vmList;
      }
    }else{
      vm.$refs[this.path] = this.el.bee || this.el;
    }
  }
}
